import React from 'react';
import bannerwallace from '../assets/img/WALLACE/wallace.jpg';
import pic1 from '../assets/img/WALLACE/1.png';
import pic2 from '../assets/img/WALLACE/3.png';
import pic3 from '../assets/img/WALLACE/4.png';
import pic4 from '../assets/img/WALLACE/11.png';
import pic5 from '../assets/img/WALLACE/VERTICAL1.jpg';
import pic6 from '../assets/img/WALLACE/VERTICAL2.jpg';
function Wallace() {

return (
    <div className="fo" > 
      <div className="overlayt banim" id="bannerserv">  
        <img className="img-fluid" src={bannerwallace} alt="bannerwallace" />
      </div>
    
    <div className="textwrap">
    <h1>Whisky Wallace</h1>
    <p>Whisky del oxxo y para el oxxo, no sólo es coincidencia que Wallace, solo se encuentre a la venta en este
    famoso cuatro letras, ya que una de las premisas básicas de la marca es que pueda encontrarse en cada esquina, 
    y literalmente México, es territorio "OXXO".</p>
    <p>Durante la pandemia nace Wallace, sin embargo meses después llega a nosotros, 
    nuestro principal reto: redefinir el estilo de comunicación de la marca, para mostrar a Wallace como la opción que te 
    acompañará y te hará disfrutar cada momento al máximo. Nuestra comunicación va enfocada a un grupo que comienza a degustar
    esta bebida, un público joven e intrépido al igual que nuestra marca.</p>
    </div>

    <div className="pics-wrapper2">  
          <div className="row"> 
          	<div className="column2">
	              <div className="img-hover-zoom">
	              		<img className="img-fluid border2 " src={pic5} alt="Botella Wallace" /> 
	              </div>
	          </div>
              <div className="column2">
	              <div className="img-hover-zoom"> 
	              		<img className="img-fluid border" src={pic1} alt="Whiskey" />        
	              </div>
	              <div className="img-hover-zoom">
	              		<img className="img-fluid border" src={pic2} alt="Frase Wallace" />  
	              </div>
	          </div>
              <div className="column2">
	              <div className="img-hover-zoom">
	              		<img className="img-fluid border" src={pic3} alt="Wallace Bebida" /> 
	              </div>
	              <div className="img-hover-zoom"> 
	              		<img className="img-fluid border" src={pic4} alt="OxxoWallace" />
	              </div>
	          </div>
	          
	          <div className="column2">
	              <div className="img-hover-zoom ">
	              		<img className="img-fluid border2" src={pic6} alt="Wallace Cocktail" /> 
	              </div>
	          </div>
                         
          </div>
            
    </div>
    
    </div>


  );
}
  
export default Wallace;