import React from 'react';
import bannerPalacio from '../assets/img/PALACIO/palacio.jpg';

import pic1 from '../assets/img/PALACIO/palacio1.jpg';
import pic2 from '../assets/img/PALACIO/palacio2.jpg';
import pic3 from '../assets/img/PALACIO/palacio3.jpg';
import pic4 from '../assets/img/PALACIO/palacio4.jpg';
import pic5 from '../assets/img/PALACIO/palacio5.jpg';
import pic6 from '../assets/img/PALACIO/palacio6.jpg';
import pic7 from '../assets/img/PALACIO/palacio7.jpg';
import pic8 from '../assets/img/PALACIO/palacio8.jpg';



function Palacio() {

return (
    <div > 
      <div className="overlayt banim" id="bannerserv">  
        <img className="img-fluid" src={bannerPalacio} alt="bannerPalaciodehierro" />
      </div>
   

	    <div className="textwrap">
	    	<h1>Palacio de Hierro</h1>
		    <p>Llegó el 14 de febrero,  nuestra fecha favorita para celebrar el amor y la amistad. 
		    De la mano de El Palacio de Hierro, creamos una experiencia BTL donde el ingrediente principal fue la suerte 
		    en el amor. Desarrollamos un detalle único para que, independientemente de que fueran un Lover o un Loner,
		     pudieran disfrutarlo en compañía de quien ellos quisieran y así el amor inundará sus vidas ese San Valentín.
		     Enamoráte en Palacio. </p>
		     <p>Y no sólo se trata de enamorarte, también de disfrutar con ese niño interior a través de nuestras activaciones 
		     durante el día del niño. </p>
	    </div>

	    <div className="pics-wrapper2 text-center">  
          <div className="row"> 
              <div className="column2">
	              <div className="img-hover-zoom"> 
	              		<img className="img-fluid border" src={pic5} alt="Kit Palacio" />        
	              </div>
	              <div className="img-hover-zoom">
	              		<img className="img-fluid border" src={pic4} alt="Juego Palacio de Hierro" />  
	              </div>
	          </div>     
	          <div className="column2">
	              <div className="img-hover-zoom"> 
	              		<img className="img-fluid border" src={pic1} alt="Palacio 14 Febrero" />        
	              </div>
	              <div className="img-hover-zoom">
	              		<img className="img-fluid border" src={pic3} alt="Juego Niños Palacio de Hierro" />  
	              </div>
	          </div>
	          <div className="column2">
	              <div className="img-hover-zoom"> 
	              		<img className="img-fluid border" src={pic7} alt="Regalo Palacio de Hierro" />        
	              </div>
	              <div className="img-hover-zoom">
	              		<img className="img-fluid border" src={pic2} alt="Activación dia del niño" />  
	              </div>
	          </div>
	          <div className="column2">
	              <div className="img-hover-zoom"> 
	              		<img className="img-fluid border" src={pic8} alt="Juego Cartas Palacio" />        
	              </div>
	              <div className="img-hover-zoom">
	              		<img className="img-fluid border" src={pic6} alt="Kit Niños Palacio de Hierro" />  
	              </div>
	          </div>            
          </div>       
    	</div>

    	
    </div>


  );
}
  
export default Palacio;