import React from 'react';
import bannerdouglas from '../assets/img/DOUGLAS/DOUGLAS.jpg';
import pic1 from '../assets/img/DOUGLAS/1.png';
import pic2 from '../assets/img/DOUGLAS/3.png';
import pic3 from '../assets/img/DOUGLAS/11.png';
import pic4 from '../assets/img/DOUGLAS/5.png';
import pic5 from '../assets/img/DOUGLAS/VERTICAL1.jpg';
import pic6 from '../assets/img/DOUGLAS/VERTICAL2.jpg';
function Douglas() {

return (
    <div > 
      <div className="overlayt banim" id="bannerserv">  
        <img className="img-fluid" src={bannerdouglas} alt="bannerDouglas" />
      </div>
    

    <div className="textwrap">
    <h1>Douglas</h1>
    <p>Con Douglas nos convertimos en la generación que no le tiene miedo a nada, junto con la llegada de la pandemia al mundo,
     recibimos esta marca de whisky. El reto: enseñar a nuestra comunidad a redefinir la forma de vivir cada momento
      acompañado de los amigos y por qué no, a la familia Douglas. </p>
    </div>

    <div className="pics-wrapper2">  
          <div className="row"> 
          		<div className="column2 ">
	              <div className="img-hover-zoom">
	              		<img className="img-fluid border2" src={pic5} alt="Douglas Whiskey" /> 
	              </div>
	          </div>
              <div className="column2 ">
	              <div className="img-hover-zoom "> 
	              		<img className="img-fluid border " src={pic1} alt="Douglas Lata" />        
	              </div>
	              <div className="img-hover-zoom">
	              		<img className="img-fluid border" src={pic2} alt="Douglas Cocktail" />  
	              </div>
	          </div>

              <div className="column2 ">
	              <div className="img-hover-zoom ">
	              		<img className="img-fluid border" src={pic3} alt="Botella Douglas" /> 
	              </div>
	              <div className="img-hover-zoom"> 
	              		<img className="img-fluid border" src={pic4} alt="Douglas preparado" />
	              </div>
	          </div>
	          
	          <div className="column2">
	              <div className="img-hover-zoom ">
	              		<img className="img-fluid border2" src={pic6} alt="Douglas presentación" /> 
	              </div>
	          </div>
	          
                         
          </div>
            
    </div>
    </div>


  );
}
  
export default Douglas;