import React from 'react';
import bannermerco from '../assets/img/MERCO/MERCO.jpg';
import pic1 from '../assets/img/MERCO/1.jpg';
import pic2 from '../assets/img/MERCO/4.png';
import pic3 from '../assets/img/MERCO/3.jpg';
import pic4 from '../assets/img/MERCO/2.jpg';
import pic5 from '../assets/img/MERCO/menu.png';
import pic6 from '../assets/img/MERCO/familia.jpg';
function Merco() {

return (
    <div > 
      <div className="overlayt banim" id="bannerserv">  
        <img className="img-fluid" src={bannermerco} alt="bannerMerco" />
      </div>
    
    <div className="textwrap">
    <h1>SUPER MEGO</h1>
    <p> Todo lo que necesitas para cuidar de tu familia, sí, eso es SUPER MEGO, ubicado en la ciudad de Guadalajara, 
    con dos sucursales. En el 2020 se acercaron a nosotros con un reto: darse a conocer en ciertas zonas de la ciudad y 
    promocionar su servicio a domicilio, haciéndolo de una manera diferente y no parecer una tiendita más que lo único que 
    quiere hacer es venderte, sino mostrar que es un negocio enfocado en cuidar de la familia, donde puedes encontrar una
     amplia variedad de comida fresca, recién cocinada y todos los víveres necesarios para tu hogar.
</p>
    </div>

    <div className="pics-wrapper2">  
          <div className="row"> 
          	<div className="column2">
	              <div className="img-hover-zoom">
	              		<img className="img-fluid border2" src={pic5} alt="Menú SuperMego" /> 
	              </div>
	          </div>
              <div className="column2">
	              <div className="img-hover-zoom"> 
	              		<img className="img-fluid border " src={pic1} alt="Tip SuperMego" />        
	              </div>
	              <div className="img-hover-zoom">
	              		<img className="img-fluid border" src={pic2} alt="Postre SuperMego" />  
	              </div>
	          </div>
              <div className="column2">
	              <div className="img-hover-zoom">
	              		<img className="img-fluid border" src={pic3} alt="Comida SuperMego" /> 
	              </div>
	              <div className="img-hover-zoom"> 
	              		<img className="img-fluid border" src={pic4} alt="Publicidad SuperMego" />
	              </div>
	          </div>
	          
	          <div className="column2">
	              <div className="img-hover-zoom ">
	              		<img className="img-fluid border2" src={pic6} alt="Familia SuperMego" /> 
	              </div>
	          </div>
                         
          </div>
            
    </div>
    
    </div>


  );
}
  
export default Merco;