import React from 'react';
import bannerhaus from '../assets/img/HAUS/haus.jpg';
import pic1 from '../assets/img/HAUS/1.png';
import pic2 from '../assets/img/HAUS/2.png';
import pic3 from '../assets/img/HAUS/3.png';
import pic4 from '../assets/img/HAUS/4.jpg';
import pic5 from '../assets/img/HAUS/vertical.jpg';
import pic6 from '../assets/img/HAUS/VERTICAL2.jpg';
function Haus() {

return (
    <div className="fo" > 
      <div className="overlayt banim" id="bannerserv">  
        <img className="img-fluid" src={bannerhaus} alt="bannerHaus" />
      </div>
    
    <div className="textwrap">
    <h1>Haus Lite</h1>
    <p>Tiendas especializadas en iluminación llegaron a dar luz y brillo a Gallo amarillo. El reto para la agencia fue
    construir la comunicación de ambas marcas dando una personalidad diferente a cada una de ellas, preservando la misma
     esencia. El objetivo, incrementar el tráfico dentro de los puntos de venta localizados en la Zona Metropolitana de 
     Guadalajara, además de la generación de prospectos a nivel nacional, enfocándonos principalmente al sector de 
     arquitectura y construcción.</p>
    </div>

    <div className="pics-wrapper2">  
          <div className="row"> 
          	<div className="column2">
	              <div className="img-hover-zoom ">
	              		<img className="img-fluid border2" src={pic6} alt="Haus iluminación" /> 
	              </div>
	          </div>
              <div className="column2">
	              <div className="img-hover-zoom"> 
	              		<img className="img-fluid border" src={pic1} alt="Haus Decoración " />        
	              </div>
	              <div className="img-hover-zoom">
	              		<img className="img-fluid border" src={pic2} alt="Haus Proyectos" />  
	              </div>
	          </div>
              <div className="column2">
	              <div className="img-hover-zoom">
	              		<img className="img-fluid border" src={pic3} alt="Haus Exteriores" /> 
	              </div>
	              <div className="img-hover-zoom"> 
	              		<img className="img-fluid border" src={pic4} alt="Haus Interiores" />
	              </div>
	          </div>
	          <div className="column2">
	              <div className="img-hover-zoom">
	              		<img className="img-fluid border2" src={pic5} alt="Haus Lámpara" /> 
	              </div>
	          </div>
	          
                         
          </div>
            
    </div>
    
    </div>


  );
}
  
export default Haus;