import React from 'react';
import bannerrancho from '../assets/img/RANCHO/RANCHITO.jpg';
import pic1 from '../assets/img/RANCHO/RANCHO1.png';
import pic2 from '../assets/img/RANCHO/ran62.png';
import pic3 from '../assets/img/RANCHO/RANCHO2.png';
import pic4 from '../assets/img/RANCHO/CRISTAL.png';
import pic5 from '../assets/img/RANCHO/ran3.jpg';
import pic6 from '../assets/img/RANCHO/RANCHO4.jpg';
function Rancho() {

return (
    <div className="fo" > 
      <div className="overlayt banim" id="bannerserv">  
        <img className="img-fluid" src={bannerrancho} alt="bannerRancho" />
      </div>
    
    <div className="textwrap">
    <h1>Rancho Escondido</h1>
    <p>Rancho Escondido, el licor de agave más vendido en México, fue a fines del 2019 cuando el gran tío Rancho llegó a nosotros,
     venía con algunas complicaciones debido a que su página  de facebook con más de 85,000 seguidores  había sido, 
      bloqueada y por ende perdieron a toda su comunidad. Aunado a ello se nos presentó el segundo reto, 
      debía redefinirse el estilo de comunicación de la marca, crear el tono y estilo adecuado para ella,
       hoy en el 2021, hemos alcanzado y superado la cantidad de seguidores, la presencia de de Rancho es cada vez más fuerte 
       y con un alto engagement con la comunidad.</p>
    </div>

    <div className="pics-wrapper2">  
          <div className="row"> 
          	<div className="column2">
	              <div className="img-hover-zoom" >
	              		<img className="img-fluid border2" src={pic5} alt="Lata RanchoEscondido" /> 
	              </div>
	          </div>
              <div className="column2" >
	              <div className="img-hover-zoom" > 
	              		<img className="img-fluid border" src={pic1} alt="Variedad RanchoEscondido" />        
	              </div>
	              <div className="img-hover-zoom" >
	              		<img className="img-fluid border" src={pic2} alt="Identidad RanchoEscondido" />  
	              </div>
	          </div>
              <div className="column2" >
	              <div className="img-hover-zoom" >
	              		<img className="img-fluid border" src={pic3} alt="Seguridad RanchoEscondido" /> 
	              </div>
	              <div className="img-hover-zoom" > 
	              		<img className="img-fluid border" src={pic4} alt="Cristal RanchoEscondido" />
	              </div>
	          </div>
	          
	          <div className="column2" >
	              <div className="img-hover-zoom ">
	              		<img className="img-fluid border2" src={pic6} alt="Original RanchoEscondido" /> 
	              </div>
	          </div>
                         
          </div>
            
    </div>
    
    </div>


  );
}
  
export default Rancho;