import React from 'react';
import './App.css';
import './index.css';

import Inicio from './components/Inicio.js';
import {BrowserRouter as Router, Switch, Route} from "react-router-dom";
import './App.css';
import Douglas from './components/Douglas.js';
import Palacio from './components/Palacio.js';
import Rancho from './components/Rancho.js';
import Merco from './components/Merco.js';
import Wallace from './components/Wallace.js';
import Onward from './components/Onward.js';
import Punto from './components/Punto.js';
import Haus from './components/Haus.js';
import CajaYanga from './components/CajaYanga.js';

function App() {
  return (
     <Router>
      <div className="App">
         
        
        <Switch>
         <Route exact path="/">
            <Inicio />
          </Route>
          <Route path="/GrandDouglas">
            <Douglas />
          </Route>
          <Route path="/PalaciodeHierro">
            <Palacio />
          </Route>
          <Route path="/RanchoEscondido">
            <Rancho/>
          </Route>
          <Route path="/SuperMego">
            <Merco/>
          </Route>
          <Route path="/Wallace">
            <Wallace/>
          </Route>
          <Route path="/Onward">
            <Onward/>
          </Route>.
          <Route path="/PuntoLight">
            <Punto/>
          </Route>
          <Route path="/Haus">
            <Haus/>
          </Route>
          <Route path="/CajaYanga">
            <CajaYanga/>
          </Route>

        </Switch>
         
         
        

         
      </div>
</Router> 
   
  );
}

export default App;
